import React from 'react'
import classNames from 'classnames'

class MenuButton extends React.Component {
  render() {
    const { menuIsOpen, toggleMenu } = this.props

    return (
      <div
        className={classNames({
          'Menu-button': true,
          'active': menuIsOpen
        })}
        onClick={toggleMenu}
      >
        <svg viewBox="0 0 38 26" xmlns="http://www.w3.org/2000/svg">
          <path d="M36,4H2A2,2,0,0,1,2,0H36A2,2,0,0,1,36,4Z"/>
          <path d="M36,15H2a2,2,0,0,1,0-4H36A2,2,0,0,1,36,15Z"/>
          <path d="M36,26H2a2,2,0,0,1,0-4H36A2,2,0,0,1,36,26Z"/>
        </svg>
      </div>
    )
  }
}

export default MenuButton


