import React from 'react'
import Hammer from 'hammerjs'
import classNames from 'classnames'
import '../App.css'

class MenuDropdown extends React.Component {
  componentDidMount() {
    this.hammer = Hammer(this.refs.dropdownMenu)
    this.hammer.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL })
    this.hammer.on('swipeup', (e) => this.props.menuSwipeClosed(e))
  }

  render() {
    const { 
      menuIsOpen,
      filters,
      toggleFilter,
      sortBy, 
      setSortBy } = this.props

    return (
      <div ref="dropdownMenu" className={classNames({
        'Menu-dropdown': true,
        'hidden': !menuIsOpen
      })}>
        <div className="Submenu-sort">
          <div className="Submenu-title">SORT BY</div>
          <div className="Submenu-contents">
            <div 
              onClick={() => setSortBy('title')}
              className={classNames({
                'Submenu-item': true,
                'active': sortBy === 'title'
              })}>Title</div>
            <div 
              onClick={() => setSortBy('composer')}
              className={classNames({
                'Submenu-item': true,
                'active': sortBy === 'composer'
              })}>Composer</div>
            <div
              onClick={() => setSortBy('year')}
              className={classNames({
                'Submenu-item': true,
                'active': sortBy === 'year'
              })}>Year</div>
          </div>
        </div>
        <div className="Submenu-filter">
          <div className="Submenu-title">FILTERS</div>
          <div className="Submenu-contents">
            <div
              onClick={() => toggleFilter('jazz-tunes')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('jazz-tunes') > -1
              })}>Jazz compositions</div>
            <div
              onClick={() => toggleFilter('standards')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('standards') > -1
              })}>Standards</div>
            <div
              onClick={() => toggleFilter('blues')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('blues') > -1
              })}>Blues</div>
            <div
              onClick={() => toggleFilter('rhythm')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('rhythm') > -1
              })}>Rhythm changes</div>
            <div
              onClick={() => toggleFilter('latin')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('latin') > -1
              })}>Latin</div>
            <div
              onClick={() => toggleFilter('waltz')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('waltz') > -1
              })}>Waltzes</div>
            <div
              onClick={() => toggleFilter('holiday')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('holiday') > -1
              })}>Holiday songs</div>
            <div
              onClick={() => toggleFilter('all')}
              className={classNames({
                'Submenu-item': true,
                'active': filters.indexOf('all') > -1
              })}>All</div>
          </div>
        </div>
      </div>
    )
  }
}

export default MenuDropdown


