import React from 'react'
import '../App.css'

class RandomScreen extends React.Component {
  render() {
    const { randomTune, setNewRandomTune } = this.props

    if (randomTune) return (
      <div className="Random-screen">
        <div className="Random-tune" onClick={setNewRandomTune}>
          <span className="Tune-title">
            {randomTune.title}
          </span>
          <span className="Tune-detail">
            {randomTune.composer} ({randomTune.year})
          </span>
        </div>
      </div>
    )
    else return (
      <div className="Random-screen">
        <div className="Random-tune" onClick={setNewRandomTune}>
          <span className="Tune-detail">Loading random tune...</span>
        </div>
      </div>
    )
  }
}

export default RandomScreen


